@import "~react-responsive-modal/styles";

.rcb-antd-modal-mask,
.rcb-antd-modal-wrap,
.rtg-antd-message {
    z-index: 99999999;
}

.rtg-example-container {
    margin-top: 10px;
    padding: 10px 20px 0;
    border-top: 1px solid;
    border-bottom: 1px solid;
    border-color: #dddddd;
    background: #f9f9f9;
}

.rtg-dialog-content {
    padding: 24px;
    background: #fff;
    height: 100%;
    display: flex;
    flex-direction: column;

    > * {
        flex: 0 1 auto;
    }

    > .rtg-grow {
        flex-grow: 1;
    }
}

.rtg-thumbnails-list {
    li[data-id] {
        display: flex;
        align-items: center;
        padding: 5px 10px 5px 3px;

        sup {
            color: #b9b9b9;
            min-width: 20px;
            margin-right: 5px;
        }

        img {
            width: auto;
            height: 30px;
            margin-right: 10px;
        }

        &:nth-child(even) {
            background: #fbfbfb;
        }

        &:after {
            clear: both;
            content: "";
            display: block;
        }

        &:hover {
            background: #1890ff;
            color: white;
            cursor: pointer;

            sup,
            ul {
                opacity: 0;
            }
        }
    }
}

.rtg-highlight-placeholders span {
    background: #e6e6e6;
}

ul.rtg-analyse-result {
    margin-left: auto;
    border: 1px solid #e8e8e8;
    border-radius: 5px;
    overflow: hidden;
    background: white;

    > li {
        display: inline-block;
        padding: 5px;
        border-right: 1px solid #f8f8f8;
        min-width: 50px;
        text-align: center;
        margin: 0;

        &:last-of-type {
            border-right: 0;
        }
    }

    > .rtg-legend {
        background: #1890ff;
        color: white;
        min-width: initial;
        padding: 5px 10px;
    }
}

.rtg-dialog-single-preview {
    height: 36px;
    width: auto;
    float: right;
    margin-top: 2px;
}

.rtg-single {
    h3 {
        margin-top: 0;
        font-weight: bold;
        border-bottom: 1px solid #f0f2f5;
        padding-bottom: 23px;
    }

    .rtg-size-img {
        float: left;
        padding-right: 20px;
        text-align: center;

        div {
            padding-bottom: 10px;
            font-size: 13px;
            letter-spacing: 3px;
        }

        img {
            margin-bottom: 10px;
        }
    }

    .rtg-container {
        max-width: 550px;

        code {
            padding: 2px 10px;
            border-radius: 99px;
            font-weight: normal;
        }

        > div {
            &:first-of-type {
                padding-bottom: 5px;
                border-bottom: 1px solid #ededed;
                margin-bottom: 3px;
            }

            &.unused {
                opacity: 0.7;
            }

            padding-bottom: 6px;

            a {
                text-decoration: none;

                &:hover {
                    text-decoration: underline;
                }
            }

            div:not(.clear) {
                float: right;
            }
        }
    }
}

.rtg-attachment-compat {
    .alignleft {
        padding-top: 6px;
    }
}

// Update notice
tr.rtg-update-notice {
    th {
        padding: 0;
    }

    .notice {
        margin: 0 20px 10px 26px;
        border-top: 1px solid #ffb900;
        box-shadow: 4px 4px 4px #ffb9001a, 1px 1px 0px #ffb900;

        p {
            margin: 0;
        }
    }
}
